.nav-link,
.dropdown-item {
  cursor: pointer;
}

.tabs {
  display: none;
}
.shadowB {
  box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.w-20 {
  width: 230px;
}

.w-20:hover {
  transition: all 0.5s;
  filter: sepia(0.7);
  cursor: pointer;
}

.thumb-title {
  margin-top: 5px;
}
